'use client';

import React, { useState } from 'react';
import {
  Box,
  Paper,
  Typography,
  TextField,
  Button,
  Tab,
  Tabs,
  Divider,
  Link,
  InputAdornment,
  IconButton,
  Alert,
} from '@mui/material';
import {
  Visibility,
  VisibilityOff,
  AccountCircle,
  Lock,
  Security,
  Phone,
} from '@mui/icons-material';
import { useRouter } from 'next/navigation';

interface TabPanelProps {
  children?: React.ReactNode;
  index: number;
  value: number;
}

function TabPanel(props: TabPanelProps) {
  const { children, value, index, ...other } = props;

  return (
    <div
      role="tabpanel"
      hidden={value !== index}
      id={`simple-tabpanel-${index}`}
      aria-labelledby={`simple-tab-${index}`}
      {...other}
    >
      {value === index && <Box sx={{ pt: 3 }}>{children}</Box>}
    </div>
  );
}

const LoginComponent: React.FC = () => {
  const [tabValue, setTabValue] = useState(1); // Default to ULB Login
  const [showPassword, setShowPassword] = useState(false);
  const [showOTP, setShowOTP] = useState(false);
  const [formData, setFormData] = useState({
    userId: '',
    password: '',
    otp: '',
    captcha: '',
  });
  const [errors, setErrors] = useState<{ [key: string]: string }>({});
  const [loading, setLoading] = useState(false);
  
  const router = useRouter();

  const handleTabChange = (event: React.SyntheticEvent, newValue: number) => {
    setTabValue(newValue);
    setFormData({ userId: '', password: '', otp: '', captcha: '' });
    setErrors({});
    setShowOTP(false);
  };

  const handleInputChange = (field: string, value: string) => {
    setFormData(prev => ({ ...prev, [field]: value }));
    // Clear error when user starts typing
    if (errors[field]) {
      setErrors(prev => ({ ...prev, [field]: '' }));
    }
  };

  const validateForm = (): boolean => {
    const newErrors: { [key: string]: string } = {};

    if (!formData.userId.trim()) {
      newErrors.userId = 'User ID is required';
    } else if (formData.userId.length !== 6 || !/^\d{6}$/.test(formData.userId)) {
      newErrors.userId = 'User ID must be exactly 6 digits';
    }

    if (!formData.password.trim()) {
      newErrors.password = 'Password is required';
    } else if (formData.password.length < 8 || formData.password.length > 12) {
      newErrors.password = 'Password must be 8-12 characters';
    }

    if (showOTP) {
      if (!formData.otp.trim()) {
        newErrors.otp = 'OTP is required';
      } else if (!/^\d{6}$/.test(formData.otp)) {
        newErrors.otp = 'OTP must be 6 digits';
      }

      if (!formData.captcha.trim()) {
        newErrors.captcha = 'CAPTCHA is required';
      }
    }

    setErrors(newErrors);
    return Object.keys(newErrors).length === 0;
  };

  const handleGenerateOTP = () => {
    if (formData.userId && formData.password) {
      if (validateForm()) {
        setShowOTP(true);
        // Simulate OTP generation
        alert(`OTP sent to registered mobile number ending with ***${Math.floor(Math.random() * 100).toString().padStart(2, '0')}`);
      }
    } else {
      setErrors({ 
        userId: !formData.userId ? 'Enter User ID first' : '', 
        password: !formData.password ? 'Enter Password first' : '' 
      });
    }
  };

  const handleLogin = () => {
    if (validateForm()) {
      setLoading(true);
      // Simulate login process
      setTimeout(() => {
        setLoading(false);
        router.push('/dashboard');
      }, 2000);
    }
  };

  return (
    <Box
      sx={{
        minHeight: '100vh',
        background: 'linear-gradient(135deg, #1e3c72 0%, #2a5298 100%)',
        display: 'flex',
        alignItems: 'center',
        justifyContent: 'center',
        padding: 2,
      }}
    >
      {/* Main Login Container */}
      <Paper
        elevation={10}
        sx={{
          width: '100%',
          maxWidth: 450,
          borderRadius: 3,
          overflow: 'hidden',
          background: 'white',
        }}
      >
        {/* Header with Government Branding */}
        <Box
          sx={{
            background: 'linear-gradient(135deg, #1565c0 0%, #0d47a1 100%)',
            color: 'white',
            p: 3,
            textAlign: 'center',
          }}
        >
          <Box sx={{ display: 'flex', alignItems: 'center', justifyContent: 'center', mb: 2 }}>
            <AccountCircle sx={{ fontSize: 60, mr: 2 }} />
            <Box>
              <Typography variant="h6" sx={{ fontWeight: 'bold', lineHeight: 1.2 }}>
                Directorate of Municipal Administration
              </Typography>
              <Typography variant="body2" sx={{ opacity: 0.9 }}>
                GOVERNMENT OF KARNATAKA
              </Typography>
            </Box>
          </Box>
          
          <Typography variant="h5" sx={{ fontWeight: 'bold', mb: 1 }}>
            KMDS ERP System
          </Typography>
          <Typography variant="body2" sx={{ opacity: 0.9 }}>
            Karnataka Municipal Data Society
          </Typography>
        </Box>

        {/* Login Tabs */}
        <Box sx={{ borderBottom: 1, borderColor: 'divider' }}>
          <Tabs
            value={tabValue}
            onChange={handleTabChange}
            variant="fullWidth"
            textColor="primary"
            indicatorColor="primary"
          >
            <Tab label="Citizen" />
            <Tab label="ULB Login" />
          </Tabs>
        </Box>

        {/* Login Forms */}
        <Box sx={{ p: 3 }}>
          
          {/* Citizen Login */}
          <TabPanel value={tabValue} index={0}>
            <Alert severity="info" sx={{ mb: 2 }}>
              Citizen portal is under development. Please use ULB Login.
            </Alert>
          </TabPanel>

          {/* ULB Login */}
          <TabPanel value={tabValue} index={1}>
            <Box component="form" sx={{ display: 'flex', flexDirection: 'column', gap: 3 }}>
              
              {/* User ID Field */}
              <TextField
                fullWidth
                label="User ID *"
                variant="outlined"
                value={formData.userId}
                onChange={(e) => handleInputChange('userId', e.target.value)}
                error={!!errors.userId}
                helperText={errors.userId || 'Enter your 6-digit User ID'}
                InputProps={{
                  startAdornment: (
                    <InputAdornment position="start">
                      <AccountCircle color="primary" />
                    </InputAdornment>
                  ),
                }}
                inputProps={{ maxLength: 6 }}
              />

              {/* Password Field */}
              <TextField
                fullWidth
                label="Password *"
                type={showPassword ? 'text' : 'password'}
                variant="outlined"
                value={formData.password}
                onChange={(e) => handleInputChange('password', e.target.value)}
                error={!!errors.password}
                helperText={errors.password || '8-12 characters'}
                InputProps={{
                  startAdornment: (
                    <InputAdornment position="start">
                      <Lock color="primary" />
                    </InputAdornment>
                  ),
                  endAdornment: (
                    <InputAdornment position="end">
                      <IconButton
                        onClick={() => setShowPassword(!showPassword)}
                        edge="end"
                      >
                        {showPassword ? <VisibilityOff /> : <Visibility />}
                      </IconButton>
                    </InputAdornment>
                  ),
                }}
                inputProps={{ maxLength: 12 }}
              />

              {/* Generate OTP Button */}
              <Button
                variant="contained"
                startIcon={<Phone />}
                onClick={handleGenerateOTP}
                disabled={!formData.userId || !formData.password}
                sx={{ 
                  py: 1.5, 
                  textTransform: 'none',
                  background: 'linear-gradient(45deg, #2196F3 30%, #21CBF3 90%)',
                }}
              >
                Generate OTP
              </Button>

              {/* OTP and CAPTCHA Fields (shown after OTP generation) */}
              {showOTP && (
                <>
                  <Divider sx={{ my: 1 }}>
                    <Typography variant="body2" color="text.secondary">
                      Enter OTP and CAPTCHA
                    </Typography>
                  </Divider>

                  <TextField
                    fullWidth
                    label="OTP *"
                    variant="outlined"
                    value={formData.otp}
                    onChange={(e) => handleInputChange('otp', e.target.value)}
                    error={!!errors.otp}
                    helperText={errors.otp || 'Enter the 6-digit OTP sent to your mobile'}
                    InputProps={{
                      startAdornment: (
                        <InputAdornment position="start">
                          <Security color="primary" />
                        </InputAdornment>
                      ),
                    }}
                    inputProps={{ maxLength: 6 }}
                  />

                  {/* CAPTCHA Simulation */}
                  <Box sx={{ display: 'flex', gap: 2, alignItems: 'flex-start' }}>
                    <Box
                      sx={{
                        bgcolor: 'grey.200',
                        p: 2,
                        borderRadius: 1,
                        textAlign: 'center',
                        minWidth: 120,
                        border: '2px dashed #ccc',
                      }}
                    >
                      <Typography variant="h6" sx={{ color: 'primary.main', fontWeight: 'bold' }}>
                        7H3K9
                      </Typography>
                      <Typography variant="caption" color="text.secondary">
                        CAPTCHA
                      </Typography>
                    </Box>
                    
                    <TextField
                      fullWidth
                      label="Enter CAPTCHA *"
                      variant="outlined"
                      value={formData.captcha}
                      onChange={(e) => handleInputChange('captcha', e.target.value)}
                      error={!!errors.captcha}
                      helperText={errors.captcha || 'Enter the text shown in CAPTCHA'}
                      inputProps={{ maxLength: 5 }}
                    />
                  </Box>

                  {/* Login Button */}
                  <Button
                    variant="contained"
                    size="large"
                    onClick={handleLogin}
                    disabled={loading}
                    sx={{ 
                      py: 1.5, 
                      textTransform: 'none',
                      background: 'linear-gradient(45deg, #4CAF50 30%, #8BC34A 90%)',
                      fontSize: '1.1rem',
                      fontWeight: 'bold',
                    }}
                  >
                    {loading ? 'Logging in...' : 'Login'}
                  </Button>
                </>
              )}

              {/* Footer Links */}
              <Box sx={{ textAlign: 'center', mt: 2 }}>
                <Link
                  component="button"
                  variant="body2"
                  onClick={() => alert('Forgot password functionality will be implemented')}
                  sx={{ textDecoration: 'none', '&:hover': { textDecoration: 'underline' } }}
                >
                  Forgot Password?
                </Link>
              </Box>

            </Box>
          </TabPanel>

        </Box>

        {/* Footer */}
        <Box
          sx={{
            bgcolor: 'grey.50',
            p: 2,
            textAlign: 'center',
            borderTop: '1px solid',
            borderColor: 'divider',
          }}
        >
          <Typography variant="caption" color="text.secondary">
            Â© 2025 Government of Karnataka. All rights reserved.
          </Typography>
        </Box>

      </Paper>
    </Box>
  );
};

export default LoginComponent;